"use client"
import { motion } from 'framer-motion';
import { EventCategory } from '@/types/type';
import Image from 'next/image';
import { ReactNode } from 'react';
const EventCard = ({ event, children, className }: { event: EventCategory, children: ReactNode, className: String }) => {
    return (
        <>
            <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                className={`bg-white rounded-lg shadow-md overflow-hidden border border-gray-100 hover:shadow-lg transition-shadow ${className}`}
            >
                <div className='w-full'>
                    <Image
                        src={event.imageURL}
                        alt={event.title}
                        className='w-full h-48 object-cover'
                        width={200}
                        height={300}
                    />
                </div>
                {/* رأس البطاقة */}
                < div className="border-b border-gray-100" >
                    <div className="flex items-center justify-center" >
                        {
                            event.isSpecial && (
                                <span className="bg-yellow-100 text-yellow-800 text-xs -mt-8 px-2 py-1 rounded-full" >
                                    REGISTRATIONS ARE NOW OPEN
                                </span>
                            )
                        }
                    </div>
                </div>
                {children}
            </motion.div>
        </>
    );
}
export default EventCard;